body {
  padding: 2em;
  background-color: rgb(35, 35, 35);
  color: white;
}
form {
  margin: 1em;
}
details {
  margin: 1em;
}
.synthGrid {
  display: flex;
}
.synthStep {
  cursor: pointer;
  width: 3em;
  height: 3em;
  border-radius: 10px;
  border: solid rgb(100, 0, 255) 2px;
  background-color: rgba(100, 100, 200, .6)
}
.playHead {
  border: solid magenta 2px;
}
.active {
  background-color: rgb(100, 200, 255)
}
.sequencerContainer {
  width: 35px;
  display: grid;
   grid-template-columns: auto auto auto auto auto auto auto auto auto auto auto auto auto auto auto auto;
}
.step {
  padding: .3em;
  text-align: center;
  width: 4em;
  height: 5em;
  border-radius: 2em;
  margin: .1em;
}
.underStep {
  position: absolute;
  width: 3em;
  height: 3em;
  border-radius: 10px;
  border: solid rgb(100, 0, 255) 2px;
  background-color: rgb(255, 255, 255);
  z-index: -1;
}
.dark {
  background-color: rgb(80, 0, 200);
}
.light {
  background-color: rgb(160, 0, 200);
}
#active {
  background-color: rgb(255, 170, 255);
}

.drums {
	/* display: flex;
	align-items: center;
	justify-content: center; */
}

.drum {
	background-color: rgb(174, 37, 193);
	border-radius: 1rem;
	width: 8em;
	height: 4em;
	/* display: flex;
	align-items: center; */
	justify-content: center;
	margin-left: .8rem;
	margin-right: .8rem;
	cursor: pointer;
	transition: 0.3s;
}

.drum:hover {
	opacity: 0.7;
}

.overlay {
  background-color: rgba(121, 121, 121, 0.5);
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  z-index: 4;
}


.add-form-container {
  box-sizing: border-box;
  background-color: rgba(155, 155, 155);
  text-align: left;
  width: 525px;
  padding: 1.35em 1.6em 1.5em 1.6em;
  border-radius: 10px;
  box-shadow: 0px 0px 10px 1px rgba(0, 0, 0, 0.2);
  z-index: 6;
}

.add-form-header {
  height: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.close-add-button {
  background-color: rgb(180, 180, 180);
  width: 30px;
  height: 30px;
  padding: 0;
  border-style: none;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.close-add-button:hover {
  background-color: rgb(160, 160, 160);
}

.add-form {
  margin-top: .85em;
  padding: 0 .5em;
}

.add-form > * {
  margin: .6em 0;
}

.add-input {
  width: 97%;
  height: 22px;
  padding: .1em 0 .1em .5em;
  margin-bottom: 1em;
}

.add-submit-button {
  font-size: .9em;
  text-transform: uppercase;
  background-color: rgb(199, 89, 205);
  color: white;
  padding: .5em .75em;
  border-style: none;
  border-radius: 5px;
  cursor: pointer;
}

.add-submit-button:hover {
  background-color: rgb(189, 81, 195);
}

.kit-wrapper {
  width: 70em;
  /* display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(2, 100px);
  grid-template-rows: 100px 100px 100px;
  grid-auto-flow: column; */
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

}

.key {
  background-color: #5D2E8C;
  color: #fff;
  padding: 5px;
  font-size: 150%;
  margin: 15px 10px;
  border-radius: 1rem;
	width: 7em;
	height: 3em;
  cursor: pointer;
	transition: 0.3s;
}

/*****************/
/*     Navbar    */
/*****************/
.navbar{
  background-color: rgb(35, 35, 35);
  top: 0;
  width: 100%;
  position: fixed;
  display: flex;
  align-items: center;
  box-shadow: 0 0 1em rgba(0, 0, 0, 0.1);
  z-index: 3;
}
h2{
  margin: 0;
}
.logo{
  width: 50%;
}
.icons{
  width: 50%;
  text-align: right;
}
.icons a{
  margin: 0 .75em;
}
.icon-color path{
  color: rgb(0, 0, 0);
}

.login {
  display: inline-block;
  text-align: center;
  margin-right: 1.5em;
}

/* /////////////////
  CRUD CONTAINER
//////////////////*/
.crud-container {
  border: white solid 3px;
  margin-bottom: 3em;
}